<template>
<el-container style="height: 500px; border: 1px solid #eee">
  <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
    
    <el-menu :default-openeds="['1']" class="el-menu-vertical-demo" >
    <!-- @open="handleOpen" @close="handleClose" :collapse="isCollapse"  -->
      <!-- <el-checkbox-button v-model="isCollapse">
        <i class="el-icon-plus"></i>
      </el-checkbox-button> -->
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-message"></i>
          <span>导航一</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="1-1" @click="dialogVisible = true">选项1</el-menu-item>

          <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
            <span>这是一段信息</span>
            <el-form :model="form" status-icon :rules="rules" ref="form">
              <el-form-item label="日期" :label-width="formLabelWidth" prop="date">
                <el-date-picker v-model="form.date" type="date" placeholder="选择日期" 
                format="yyyy 年 MM 月 dd 日" value-format="timestamp" style="width: 100%"></el-date-picker>
              </el-form-item>
              <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="年龄" :label-width="formLabelWidth" prop="age">
                <el-input v-model.number="form.age"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
          </el-dialog>

        </el-menu-item-group>
        <el-menu-item-group>
          <el-menu-item index="1-3">选项3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <template slot="title">选项4</template>
          <el-menu-item index="1-4-1">选项4-1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <template slot="title">导航二</template>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-setting"></i>
        <template slot="title">导航三</template>
      </el-menu-item>
    </el-menu>
  </el-aside>
  
  <el-container>
    <el-header style="text-align: right; font-size: 12px">
      <el-dropdown >
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item >查看</el-dropdown-item>
          <el-dropdown-item @click.native="formopen = true">新增</el-dropdown-item>
          <el-dropdown-item >删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span>王小虎</span>
    </el-header>

    <el-main>
      <el-table :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))" 
      @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="date" label="日期" width="140">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="120">
        </el-table-column>
        <el-table-column prop="address" label="地址" show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="right" fixed="right" width="200px">
          <template slot="header">
            <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
          </template>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
            <el-button type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>

  <el-dialog title="添加" :visible.sync="formopen">
    <el-form :model="form" :rules="rules" ref="form">
      <el-form-item label="日期" :label-width="formLabelWidth" prop="date">
        <el-date-picker v-model="form.date" type="date" placeholder="选择日期" 
        format="yyyy 年 MM 月 dd 日" value-format="timestamp" style="width: 100%"></el-date-picker>
      </el-form-item>
      <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
        <el-input v-model="form.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="地址" :label-width="formLabelWidth" prop="address">
        <el-input v-model="form.address" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="formopen = false">取 消</el-button>
      <el-button type="primary" @click="submit('form')">确 定</el-button>
      <el-button type="warning" plain @click="reset('form')">重 置</el-button>
    </div>
  </el-dialog>

</el-container>

</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }

  .el-dialog {
    text-align: left;
  }

</style>

<script>
  export default {
    data() {
      // const item = {
      //   date: '2016-05-02',
      //   name: '王小虎',
      //   address: '上海市普陀区金沙江路 1518 弄'
      // };
      const items = [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
      },{
          date: '2016-05-03',
          name: '王',
          address: '上海市普陀区金沙江路 1518 弄'
      },{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
      },{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
      },{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
      },];
      const checkage = (rule, value, callback) => {
        if (!value) {
          callback(new Error('empty error'))
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      const checkname = (rule, value, callback) => {
        if (!value) {
          callback(new Error('empty error'))
        }
        else {
          callback()
        }
      };
      return {
        isCollapse: true,
        tableData: items,
        dialogVisible: false,
        formopen: false,
        form: {
          date: '',
          name: '',
          address: ''
        },
        formLabelWidth: '50px',
        rules: {
          date: [{
            required: true,
            trigger: 'blur'
          }],
          name: [{
            validator: checkname,
            trigger: 'blur'
          }],
          age: [{
            validator: checkage, 
            trigger: 'blur'
          }],
          address: [{
            validator: checkname,
            trigger: 'blur'
          }]
        },
        multipleSelection: [],
        search:''
      }
    },
    methods: {
      handleSelectionChange (val) {
        console.log(val)
      },  
      handleClick(row) {
        console.log(row);
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },

      submit() {
        this.$refs.form.validate((valid) => {
          if (valid) {
            alert('submit success');
            this.$refs.form.resetFields();
          } else {
            console.log('submit error');
            return false;
          }
        });
      },
      reset(form) {
        this.$refs[form].resetFields();
      }
    }
  };
</script>